$cAccent: #ddaa55;
$cBlack: #121212;
$cGray: #333;
$cLightGray: rgba(#000, 0.1);

html {
  font-size: 16px;
  height: 100%;
}

body {
  background: white;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}

.content {
  margin-top: 50px;
}

.footer {
  margin-top: 50px;
}

.refresh-button {
  width: 60px;
  height: 60px;
  border: none;
  background: none;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.refresh-button.rotate {
  animation: spin 0.5s infinite linear;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.refresh-icon {
  display: inline-block;
  width: 100%;
  height: 100%;
  background-size: cover;
}

.next-button {
  width: 60px;
  height: 60px;
  border: none;
  background: none;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.next-button.move {
  transform: translateX(15px);
}

.next-icon {
  display: inline-block;
  width: 100%;
  height: 100%;
  background-size: cover;
}

.accordion-list{
  list-style: none;
  text-align: left;
  margin: 0;
  padding: 0;
  background-color: #fff;
  max-width: 30rem;
  border: none;
  overflow: hidden;

&__item{
&+&{
   border-top: 1px solid $cLightGray;
 }
}
}

.accordion-item{
  $self: &;

&--opened{
#{ $self }__icon{
  transform: rotate(180deg);
}

#{ $self }__inner{
  margin-bottom: 20px;
  max-height: 100rem;
  transition-timing-function: cubic-bezier(0.895, 0.03, 0.685, 0.22);
  transition-duration: 0.5s;
  transition-property: max-height;
}

#{ $self }__content{
  opacity: 1;
  transform: translateY(0);
  transition-delay: 0.2s;
  transition-timing-function: ease-in-out;
  transition-duration: 0.2s;
  transition-property: opacity, transform;
}
}

&__line{
   display: block;
   padding: 0.8rem 1.2rem;
   display: flex;
   justify-content: space-between;
   align-items: center;
   background-color: #fff;
   z-index: 2;
   position: relative;
 }

&__title{
   font-size: 1.6rem;
   margin: 0;
   font-weight: 700;
   color: $cBlack;
 }
